.forward-dialog-list {
    max-width: 336px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    padding: 12px 24px;
    overflow-y: auto;
}

.forward-dialog-search-list {
    max-width: 336px;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    -webkit-align-content: start;
            align-content: start;
    padding: 12px 24px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}

.forward-dialog-content {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    display: -webkit-flex;
    display: flex;
    position: relative;
    border-top: 1px solid;
    border-bottom: 1px solid;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
}

.forward-dialog-message {
    min-height: 48px;
    max-height: 96px;
}

#forward-dialog-message {
    box-shadow: 0 0 0 0;
    max-height: 150px;
    max-width: 324px;
    min-height: 24px;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding: 12px 24px 12px 24px;
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-word;
}

#forward-dialog-search {
    box-shadow: 0 0 0 0;
    max-height: 150px;
    max-width: 324px;
    min-height: 24px;
    padding: 12px 24px 12px 24px;
    white-space: nowrap;
    overflow: hidden;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
}

[contenteditable='true']#forward-dialog-search br {
    display: none;
}

[contenteditable='true']#forward-dialog-search * {
    display: inline;
    white-space: nowrap;
}

#forward-dialog-message::before,
#forward-dialog-search::before {
    /*content: attr(placeholder);*/
    color: #8e9396;
    pointer-events: none;
    -webkit-transform: translateX(20px);
            transform: translateX(20px);
    will-change: transform;
    opacity: 0;
    /*position: absolute;*/
    transition: opacity 0.15s ease-out, -webkit-transform 0.15s ease-out;
    transition: opacity 0.15s ease-out, transform 0.15s ease-out;
    transition: opacity 0.15s ease-out, transform 0.15s ease-out, -webkit-transform 0.15s ease-out;
}

#forward-dialog-message:empty::before,
#forward-dialog-search:empty::before {
    content: attr(placeholder);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    opacity: 0.75;
}

#forward-dialog-message:empty:active::before,
#forward-dialog-message:empty:focus::before,
#forward-dialog-search:empty:active::before,
#forward-dialog-search:empty:focus::before {
    opacity: 0.75;
}

#forward-dialog-message:focus,
#forward-dialog-search:focus {
    border: 0;
    outline: none;
    box-shadow: 0 0 0 0;
}

